
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  width: 100%;
  height: 100%;
}

#main {
  width: 100%;
  height: 100%;
  position: relative;
}

#Header {
  width: 100%;
  height: 80px;
  border: 1px solid blue;
}

#left {
  position: absolute;
  left: 0;
  width: 200px;
  height: 700px;
  border: 1px solid red;
}
